@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap');

.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #F4EEE6; /* Optional background color */
  font-family: 'Arial', sans-serif; /* Simple font */
}

.App-main {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.App-logo {
  max-width: 300px;
  height: auto;
  margin-bottom: 2rem;
}

h1 {
  color: #4D5D73;
  font-size: 2.5rem;
  margin: 1rem 0;
  font-weight: 500;
}

.coming-soon {
  font-size: 1.5rem;
  color: #65768C;
  font-style: italic;
  margin-top: 1rem;
}

@media screen and (max-width: 480px) {
  .App-logo {
    max-width: 200px;
  }

  h1 {
    font-size: 1.75rem;
  }

  .coming-soon {
    font-size: 1.1rem;
  }
}
